body .pnlm-container {
  width: 100% !important;
  max-width: 600px;
  height: 400px;
}

.pnlm-container__controls {
  margin-top: 4px;
  position: relative;
}
.pnlm-container__controls > div {
  width: 28px;
  padding: 4px;
  background: #fff;
  box-sizing: border-box;
  margin: 0;
  margin-top: 4px;
  background-color: #fff;
  border: 1px solid #999;
  border-color: rgba(0, 0, 0, 0.4);
  border-radius: 3px;
  cursor: pointer;
  z-index: 2;
}
.pnlm-container__controls--menu {
  cursor: pointer;
}
.pnlm-container__controls--menu img {
  width: 18px;
  height: 18px;
  pointer-events: none;
}
.pnlm-container__controls--home {
  cursor: pointer;
  margin-top: 4px;
}
.pnlm-container__controls--home img {
  width: 18px;
  height: 18px;
  pointer-events: none;
}

.pnlm-container__controls--menu-items {
  position: absolute;
  left: 44px;
  top: 0;
  z-index: 2;
  display: none;
  width: 200px !important;
}
.pnlm-container__controls--menu-items.show {
  display: block;
}
.pnlm-container__controls--menu-items ul {
  margin: 0 !important;
  list-style: none;
  padding: 0 !important;
}
.pnlm-container__controls--menu-items ul li {
  margin: 0;
  padding: 10px;
  font-size: 16px;
  border-bottom: 1px solid #ababab;
  background-color: #fff;
  cursor: pointer;
}
.pnlm-container__controls--menu-items ul li:hover {
  background-color: #000;
  color: #fff;
}
.pnlm-container__controls--menu-items ul li:last-child {
  border-bottom: 0;
}

.pnlm-container__controls--embed {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 28px;
}

.pnlm-container__controls--embed-modal {
  background: none !important;
  border: none !important;
  position: fixed;
  left: 5px;
  bottom: 5px;
  z-index: 99 !important;
  display: none;
}
.pnlm-container__controls--embed-modal.show {
  display: block;
}
.pnlm-container__controls--embed-modal textarea {
  height: 100px;
  width: 250px;
  padding: 10px;
  background: #fff;
  opacity: 1;
}

.pnlm-panorama-info {
  bottom: auto !important;
  top: 4px;
  left: auto !important;
  right: 4px;
}

.pnlm-container__hotspot-modal {
  width: 70%;
  max-width: 800px;
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 3;
  background: #fff;
  transform: translate(-50%, -50%);
  display: none;
  user-select: text;
  font-size: 16px;
}
.pnlm-container__hotspot-modal.show {
  display: block;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-heading {
  background: #000;
  position: relative;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-heading h2 {
  padding: 10px !important;
  font-size: var(--vrpress-modal-heading-font-size, 1rem) !important;
  color: #fff;
  margin-top: 0;
  margin-bottom: 0;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-heading button {
  position: absolute;
  right: 10px;
  top: 0;
  padding: 0;
  border: 0;
  background: transparent;
  display: flex;
  align-items: center;
  height: 100%;
  cursor: pointer;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-heading button img {
  width: 15px;
  color: #fff;
  pointer-events: none;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-content {
  padding: 10px;
  font-size: var(--vrpress-modal-content-font-size, inherit) !important;
  line-height: var(--vrpress-modal-content-line-height, inherit) !important;
  max-height: 60vh;
  overflow-y: scroll;
}
.pnlm-container__hotspot-modal .pnlm-container__hotspot-modal-content-inner {
  height: 100%;
  overflow-y: scroll;
}
.pnlm-container__hotspot-modal video, .pnlm-container__hotspot-modal img {
  width: 100%;
  display: block;
}

.pnlm-render-container__iframe-container {
  position: relative;
  overflow: hidden;
  width: 100%;
}
.pnlm-render-container__iframe-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.pnlm-hotspot-base {
  width: var(--vrpress-hotspot-icon-size, 40px) !important;
  height: var(--vrpress-hotspot-icon-size, 40px) !important;
  background-color: #0095ff !important;
  background-repeat: no-repeat !important;
  background-position: center center !important;
  border-radius: 50% !important;
}

.pnlm-container.modal-opened .pnlm-hotspot, .pnlm-container.modal-opened .pnlm-pointer {
  display: none !important;
}

.vrpress-modal-image-container {
  position: relative;
}
.vrpress-modal-image-container .vrpress-modal-image-container-caption {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.6);
  padding: 15px 20px;
  box-sizing: border-box;
}

.vrpress-hotspot {
  background-image: none !important;
}
.vrpress-hotspot::before {
  content: "";
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 50%;
  background-color: #0095ff;
  background-repeat: no-repeat;
  background-position: center center;
}
.vrpress-hotspot.iconType-Info::before {
  background-image: url("./../image/hotspot-info.svg");
  background-size: 9px;
}
.vrpress-hotspot.iconType-Image::before, .vrpress-hotspot.iconType-oEmbed-Image::before {
  background-image: url("./../image/images-regular.svg");
  background-size: 20px;
}
.vrpress-hotspot.iconType-Video::before, .vrpress-hotspot.iconType-oEmbed-Video::before {
  background-image: url("./../image/hotspot-video.svg");
  background-size: 20px;
}
.vrpress-hotspot.iconType-Link::before {
  background-image: url("./../image/hotspot-link.svg");
  background-size: 22px;
}
.vrpress-hotspot.iconType-Scene::before {
  background-image: url("./../image/hotspot-direction.svg");
  background-size: 22px;
}
.vrpress-hotspot.iconType-oEmbed-Document::before {
  background-image: url("./../image/file-pdf-regular.svg");
  background-size: 18px;
}
.vrpress-hotspot.rotate-0::before {
  transform: rotate(0);
}
.vrpress-hotspot.rotate-45deg::before {
  transform: rotate(45deg);
}
.vrpress-hotspot.rotate-90deg::before {
  transform: rotate(90deg);
}
.vrpress-hotspot.rotate-135deg::before {
  transform: rotate(135deg);
}
.vrpress-hotspot.rotate-180deg::before {
  transform: rotate(180deg);
}
.vrpress-hotspot.rotate-225deg::before {
  transform: rotate(225deg);
}
.vrpress-hotspot.rotate-270deg::before {
  transform: rotate(270deg);
}
.vrpress-hotspot.rotate-315deg::before {
  transform: rotate(315deg);
}

.map-container__hotspot-modal {
  width: 70%;
  max-width: 800px;
  left: 50% !important;
  top: 50% !important;
  background: #fff;
  transform: translate(-50%, -50%);
  user-select: text;
  font-size: 16px;
  display: none;
}
.map-container__hotspot-modal.show {
  display: block;
}
.map-container__hotspot-modal-heading {
  background: #000;
  position: relative;
}
.map-container__hotspot-modal-heading h2 {
  padding: 10px !important;
  font-size: 16px !important;
  color: #fff;
  margin: 0 !important;
}
.map-container__hotspot-modal-heading button {
  position: absolute;
  right: 10px;
  top: 0;
  padding: 0;
  border: 0;
  background: transparent;
  display: flex;
  align-items: center;
  height: 100%;
}
.map-container__hotspot-modal-heading button img {
  width: 15px;
  color: #fff;
  pointer-events: none;
}
.map-container__hotspot-modal-content {
  padding: 10px;
}
.map-container__hotspot-modal-content video, .map-container__hotspot-modal-content img {
  width: 100%;
  display: block;
}

.map-container__pano-navigator {
  right: 10px !important;
  top: 7px !important;
}

.streetview__controls {
  z-index: 2;
  border-radius: 3px;
}
.streetview__controls > div {
  width: 40px;
  padding: 4px;
  background: none rgb(34, 34, 34);
  box-sizing: border-box;
}
.streetview__controls--menu {
  cursor: pointer;
}
.streetview__controls--menu img {
  width: 30px;
  height: 30px;
  padding: 5px;
  pointer-events: none;
  box-sizing: border-box;
}
.streetview__controls--home {
  cursor: pointer;
  margin-top: 4px;
}
.streetview__controls--home img {
  width: 30px;
  height: 30px;
  padding: 5px;
  pointer-events: none;
  box-sizing: border-box;
}
.streetview__controls--menu-items {
  position: absolute;
  transform: translateX(calc(-100% - 5px));
  top: 0;
  background-color: #222222;
  color: #666;
  width: 150px;
  display: none;
}
.streetview__controls--menu-items ul {
  margin: 0 !important;
  list-style: none;
  padding: 0 !important;
}
.streetview__controls--menu-items ul li {
  margin: 0 !important;
  cursor: pointer;
  padding: 10px;
  font-size: 16px;
}
.streetview__controls--menu-items ul li:hover {
  color: #fff;
}
.streetview__controls--menu-items.show {
  display: block;
}

