.react-tabs__tab-list {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #ccc !important;
}
.react-tabs__tab {
  float: none;
  border-radius: 0 !important;
  margin-left: 0.5em;
  margin-bottom: 0;
  padding: 5px 10px;
  font-size: 14px;
  line-height: 1.71428571;
  font-weight: 600;
  background: #c8c8c8;
  color: #555;
  border-color: #ccc !important;
  text-decoration: none;
  white-space: nowrap;
}
.react-tabs__tab--selected {
  background: #f1f1f1 !important;
  color: #000;
}

.tabs-with-controls {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #ccc !important;
}
.tabs-with-controls .tab-controls {
  margin-left: 10px;
}
.tabs-with-controls button {
  border-radius: 0;
  border: 1px solid #000;
  cursor: pointer;
}
.tabs-with-controls button:not(:last-child) {
  border-right: 0;
}
.tabs-with-controls button.tab-previous img, .tabs-with-controls button.tab-next img {
  width: 6px;
}
.tabs-with-controls button.tab-previous:disabled, .tabs-with-controls button.tab-next:disabled {
  opacity: 0.3;
}

.pnlm-hotspot-base .hotspot {
  display: none;
}

.icon-direction-settings {
  display: flex;
  flex-wrap: wrap;
}
.icon-direction-settings__single {
  margin-right: 10px;
}
.icon-direction-settings__single .vrpress-hotspot.iconType-Scene {
  width: 30px !important;
  height: 30px !important;
  background-size: 15px !important;
}

#ubcvrpress_metabox_shortcode .inside {
  padding-top: 20px;
  padding-bottom: 20px;
}
#ubcvrpress_metabox_shortcode .inside h2 {
  padding: 0;
}
#ubcvrpress_metabox_shortcode .inside button {
  margin-left: 20px;
}

.regular-text {
  width: 100%;
  max-width: 25em !important;
}

input[type=text], select, textarea {
  box-shadow: 0 0 0 transparent;
  border-radius: 4px;
  border: 1px solid #7e8993;
  background-color: #fff;
  color: #32373c;
  min-height: 30px;
  padding: 0 8px;
  line-height: 2;
}

input[type=text].disabled, select.disabled, textarea.disabled {
  background: #ccc;
}

.custom-image-preview {
  width: 250px;
  height: 250px;
  object-fit: cover;
}

#ubc-vrpress-single-tour-admin-metabox label {
  display: block;
}
#ubc-vrpress-single-tour-admin-metabox label + input, #ubc-vrpress-single-tour-admin-metabox label + select, #ubc-vrpress-single-tour-admin-metabox label + textarea {
  margin-top: 10px;
  margin-bottom: 10px;
}

.scene-settings {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.scene-settings__general > div {
  margin-bottom: 15px;
}

#panorama-preview {
  width: 100%;
  max-width: 625px;
  height: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.post-type-ubcvrpress #poststuff #post-body.columns-2 {
  margin-right: 645px;
}
.post-type-ubcvrpress #poststuff #post-body.columns-2 #postbox-container-1 {
  width: 625px;
  margin-right: -645px;
}
.post-type-ubcvrpress #poststuff #post-body.columns-2 #postbox-container-1 #side-sortables {
  width: 625px;
  margin-right: -645px;
}

.oembed-status {
  margin-left: 5px;
}
.oembed-status.valid {
  color: green;
}
.oembed-status.invalid {
  color: red;
}

@media only screen and (max-width: 1500px) {
  .post-type-ubcvrpress #poststuff #post-body.columns-2 {
    margin-right: 0;
  }
  .post-type-ubcvrpress #poststuff #post-body.columns-2 #post-body-content {
    float: none;
  }
  .post-type-ubcvrpress #poststuff #post-body.columns-2 #postbox-container-1 {
    margin-right: 0;
    width: 100%;
    float: none;
  }
  .post-type-ubcvrpress #poststuff #post-body.columns-2 #postbox-container-1 #side-sortables {
    width: 100%;
    margin-right: 0;
  }
}
.metabox_shortcode p {
  line-height: 25px;
}
.metabox_shortcode button {
  margin-top: 10px !important;
}

table.form-table {
  margin-top: 0;
}